#header_top{
    background: $bg_header;
    position: relative;
    width: 100%;
    top: 0px;
    z-index: 4;
    .wrapper-content{
        padding: 5px 10px 5px 10px;
        position: relative;
        z-index: 5;
        background-color: $bg_header;
    }
    .td_logo{
        width: 40%;
        a{
            width: 85px;
            height: 51px;
            background: url("../img/logo_ced.svg");
            background-repeat: no-repeat;
            background-size: contain;
            display: inline-block;
        }
    }

    .td_nav_time{
        text-align: right;
        position: relative;
        .time{
            font-size: 12px;
            position: absolute;
            right: 52px;
            top: 50%;
            transform: translateY(-50%);
            p{
                margin: 0px;
                display: block;
                span{
                    text-transform: uppercase;
                }
            }
        }
    }

    #btn_nav{
        padding: 0px;
        display: inline-block;
        width: 46px;
        text-align: center;
        .btn_nav_h{
            height: 32px;
            position: relative;
            width: 30px;
            display: inline-block;
            
            span{
                position: absolute;
                height: 3px;
                display: block;
                background-color: $bg_nav;
                width: 100%;
                transform-origin: 50% 50%;
                transition: all 400ms ease;
                &:nth-child(1){
                    top: 5px;
                    transition: all 400ms ease;
                    opacity: 1;
                }
                &:nth-child(2){
                    top: 15px;
                    opacity: 1;
                    transition: all 400ms ease;
                }
                &:nth-child(3){
                    top: 25px;
                    opacity: 1;
                    transition: all 400ms ease;
                }
            }
        }
    }
}
.sticky_active{
    #header_top{
        position: fixed;
        top: 0px;
        z-index: 4;
        box-shadow: 0px 3px 3px rgba($color: #000000, $alpha: 0.15);
    }
}
#navigation{
    background: white;
    position: absolute;
    width: 100%;
    top: -100%;
    z-index: 3;
    opacity: 0;
    visibility: hidden;
    box-shadow: 0 11px 11px#0000001c;
    transition: all 400ms ease;
    ul{
        margin: 0px;
        padding: 0px 10px 10px 10px;
        li{
            border-bottom: 1px solid #eaeaea;
            a{
                padding: 15px 10px;
                color: $color-negro;
                font-size: 14px;
                display: block;
            }
            &:last-child{
                border-bottom: 0px;
            }
        }
    }
}


.active_nav{
    #btn_nav{
        span{
            position: absolute;
            display: block;
            width: 14px;
            height: 2px;
            background-color: #004f80;
            -webkit-transform: scale(0) translateY(7px);
            transform: scale(0) translateY(7px);
            -webkit-transition: all 0.25s ease;
            transition: all 0.25s ease;
            -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
            transition: all 400ms ease;
            &:nth-child(2){
                position: absolute;
                top: 5px;
                -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
                transition: all 400ms ease;
            }
            &:nth-child(3){
                -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
                margin-top: -9px;
                transition: all 400ms ease;
            }
        }
    }
    #navigation{
        opacity: 1;
        visibility: visible;
        transition: all 400ms ease;
    }
}



