.cover-distrito{
    min-height: 89px;
    background-attachment: scroll;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 20px 10px;
    color: white;
    position: relative;
    overflow: hidden;
    &:before{
        content: "";
        width: 100%;
        height: 100%;
        background: rgba($color: #000000, $alpha: 0.3);
        z-index: 1;
        display: block;
        position: absolute;
        top: 0px;
        left: 0px;
    }
    .title{
        position: relative;
        z-index: 2;
    }
    .breadcrum{
        z-index: 2;
        padding-left: 0px;
        position: relative;
        a{
            color: white;
        }
    }
}

.list_dir2{
    margin-bottom: 10px;
    padding: 10px 0px;
    border-bottom: 1px solid #eaeaea;
    .td{
        vertical-align: middle;
        .title{
            font-size: 16px;
        }
        .map{
            font-size: 11px;
            color: $color-negro;
        }
    }
    
}


.directorio_rst{
    display: table;
    padding: 10px 0px;
    margin-bottom: 10px;
    border-bottom: 1px solid #eaeaea;
    .img{
        display: table-cell;
        width: 15px;
    }
    .title{
        display: table-cell;
        vertical-align: top;
        font-weight: 300;
        padding-left: 10px;
        .t{
           
        }
        .cv{
            margin: 10px 0px;
            font-size: 14px;color: $color-verde;
        }
    }
}
.info_direct{
    font-weight: 400;
    padding-right: 90px;
    position: relative;
    span{
        position: absolute;
        right: 0px;
        top: 0px;
        font-size: 9px;
        color: $color-azul;
    }
    .l_rst{
        &:first-child{
            font-weight: 500;
        }
    }
}
.link_dir{
    padding: 10px 0px;
    display: block;
}

.info_moneda_cambio{
