.form_change_dollar{
    padding: 20px 10px;
    #btn_change{
        padding: 18px 0px;
    }
}

.result_change{
    padding: 10px 0px;
    color: $color-verde;
    .price{
        font-size: 24px;
        font-weight: 400;
        margin-bottom: 15px;
        position: relative;
        span{
            position: absolute;
            top: 0px;
            left: 0px;
        }
        input{
            color: $color-verde;
            display: block;
            padding-left:25px;
            width: 100%;
            border:0px;
            outline: none;
        }
    }
    .resut_total{
        font-size: 14px;
    }
}