%ads-demo{
    background-color: $color-amarillo;
    background-image: url('../img/logo.png');
    background-position: center center;
    background-repeat: no-repeat;
    display: block;
    margin: 0 auto;
}
.wrapper-ads{
    text-align: center;
    width: 100%;
}
.ads-skin{
    width: 1600px;
    height: 1000px;
    position: fixed;
    top: 0;
    left: 50%;
    margin-left: -800px;
    background: #fff;
}
.ads-center{
    text-align: center;
}
.demo-300x100{
    width: 300px;
    height: 100px;
    @extend %ads-demo;
}
.demo-300x250{
    width: 300px;
    height: 250px;
    @extend %ads-demo;
}
.demo-300x600{
    width: 300px;
    height: 600px;
    @extend %ads-demo;
}
.demo-970x90{
    width: 320px;
    height: 100px;
    @extend %ads-demo;
    @media screen and (min-width:980px){
        width: 970px;
        height: 90px;
    }
}
