.block_tb{
    margin: 20px 0px;
    padding: 0px 10px 20px 10px;
    border-bottom: 1px solid #eaeaea;
}
.block_tb2{
    .title,
    .subtitle{
        padding: 0px 8px;
    }
}
.title{
    font-size: 16px;
    font-weight: 500;
}
.subtitle{
    font-size: 14px;
    font-weight: 400;
}
.tb_dollar_hd{
    color: $tb_color;
    margin-bottom: 10px;
    .tb_dollar_title{
        width: 66%;
    }
    .tb_dollar_compra{
        width: 17%;
        text-align: right;
        vertical-align: middle;
    }
    .tb_dollar_venta{
        width: 17%;
        text-align: right;
        vertical-align: middle;
    }
}
.tb_bcr{
    color: $color-negro;
    margin-bottom: 10px;
    .tb_dollar_title{
        width: 66%;
        font-weight: 500;
    }
    .tb_dollar_compra{
        width: 17%;
        font-weight: 500;
        text-align: right;
        vertical-align: middle;
    }
    .tb_dollar_venta{
        width: 17%;
        font-weight: 500;
        text-align: right;
        vertical-align: middle;
    }
}
.tb_dollar{
    padding: 5px 0px;
    .tb_dollar_title{
        width: 66%;
        font-size: 12px;
        .td{
            vertical-align: middle;
            &:nth-child(1){
                width: 30px;
            }
            &:nth-child(2){
                padding-left: 5px;
            }
        }
        .btn_ag{
            width: 55%;
            padding: 0px 10px;
        }
    }
    .tb_dollar_compra{
        width: 17%;
        text-align: right;
        vertical-align: middle;
        font-size: 18px;
        font-weight: 300;
    }
    .tb_dollar_venta{
        width: 17%;
        text-align: right;
        vertical-align: middle;
        font-size: 18px;
        font-weight: 300;
    }
}
.tb_dollar_bcr{
    padding: 5px 0px;
    .tb_dollar_title{
        width: 66%;
        font-weight: 500;
    }
    .tb_dollar_compra{
        width: 17%;
        text-align: right;
        vertical-align: middle;
    }
    .tb_dollar_venta{
        width: 17%;
        text-align: right;
        vertical-align: middle;
    }
}
.btn_emergente{
    border:$border_emergente;
    border-radius: 3px;
    font-size: 10px;
    padding: 8px 18px;
    display: inline-block;
    text-align: center;
    span{
        display: block;
        color: #414547;
    }
}


.result_BCR{
    padding: 0px 10px 0px 10px;
    .block_tb{
        &:last-child{
            border-bottom: 0px;
        }
    }
}




.tb_usa_pen{
    .tb_signo {
        width: 25%;
        text-align: center;
        vertical-align: middle;
        span{
            font-size: 36px;
        }
        p{
            font-size: 24px;
            line-height: 24px;
        }
        
    }
    .active{
        color: $color-verde;
    }
    .tb_rs{
        width: 50%;
        text-align: center;
        vertical-align: middle;
        .td{
            width: 50%;
            &:first-child{
                text-align: right;
                padding-right: 10px;
            }
            &:last-child{
                padding-left: 10px;
                text-align: left;
            }
        }
    }
}