
html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: $f-main;
}

h1, h2, h3, h4, h5, h6 {
  font-family: $f-main;
}

.tcenter{
  text-align: center !important;
}
.tleft{
  text-align: left !important;
}
.tright{
  text-align: right !important;
}


.cf:before,
.cf:after {
    content: " "; /* 1 */
    display: table; /* 2 */
}

.cf:after {
    clear: both;
}
.cf {
    *zoom: 1;
}
.txt-verde{
  color: $color-verde;
}

::-moz-selection{
  background:$color-amarillo;
  color:white
}
::selection {
  background: $color-amarillo;
  color: white;
}

.c-black{
  color: $color-negro !important;
}

.responsive-img{
  width: 100%;
  display: block;
}

/*WRAPPER*/
.wrapper{
  width: 100%;
}
.wrapper-content{
  margin: 0 auto;
  @media screen and (min-width: 1200px){
    width: 1200px;
  }
}

.wrapper-table{
  display: table;
  width: 100%;
  .td{
    display: table-cell;
  }
  .va_middle{
    vertical-align: middle;
  }
}
body{
  //padding-top: 48px;
  //background-color: #52d18b;
  @media screen and (min-width:980px){
    //padding-top: 76px;
    //background-color: $color-anaranjado;
  }
}
.skin-active{
  @media screen and (min-width:980px){
    width: 1000px;
    margin: 0 auto;
  }
}



.wrapper-principal{
  background-color: $bg-content;
  @media screen and (min-width:980px){
    >.wrapper-content{
      padding: 20px;
    }
  }
}
#container-ads{
  overflow: hidden;
}
#container-ads.active{
  padding-top: 60px;
}
//MARGENES
.margin-bottom{
  margin-bottom: 30px;
  @media screen and (min-width:980px){
    margin-bottom: 30px;
  }
}
.margin-right{
  margin-right: 8px;
  @media screen and (min-width:980px){
    margin-right: 30px;
  }
}
.margin-top{
  margin-top: 8px;
  @media screen and (min-width:980px){
    margin-top: 20px;
  }
}

.padding-mobile{
  padding: 0px 8px;
  @media screen and (min-width:980px){
    padding: initial;
  }
}
.padding-top{
  padding-top: 8px;
}
.content-block{
  text-align: center;
}
.position_relative{
  position: relative;
}


.content-grid{
  width: 100%;
  @media screen and (min-width:980px){
    .grid-uno{
      display: block;
      width: 100%;
      margin-right: -316px;
      padding-right: 318px;
      float: left;
    }
    .grid-dos{
      display: block;
      float: left;
      width: 316px;
      padding-left: 16px;
    }
  }
}

.scroll-hidden{
  overflow-y: hidden;
  overflow: hidden;
  position: relative;
  @media screen and (min-width:980px){
    overflow-y: none;
  }
}

.show_mobile{
  display: inline-block;
  @media screen and (min-width:980px){
    display: none;
  }
}


.breadcrum{
  padding: 0px 8px;
}
#map {
  height: 300px;
}