.slick_casa{
    margin: 20px 0px;
    .list_casa{
        padding: 0px 10px 10px 10px;
        figure{
            a{
                display: block;
                img{
                    width: 100%;
                    display: block;
                }
            }
        }
        .distr{
            font-size: 10px;
            text-transform: uppercase;
        }
        .name{
            font-size: 14px;
            color: $color-azul;
            font-weight: 500;
            margin-bottom: 20px;
        }
    }
}